.about_container{width: 100%; max-width: 400px; margin: 100px auto 0; padding: 20px; border-radius: 5px; background: #fff; font-weight: 300; box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25), 0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);}
